@font-face {
    font-family: "Libre Franklin";
    font-style: normal;
    font-weight: 700;
    src: url("../fonts/libre-franklin-v7-latin-700.eot");
    src: local(""),
        url("../fonts/libre-franklin-v7-latin-700.eot?#iefix")
            format("embedded-opentype"),
        url("../fonts/libre-franklin-v7-latin-700.woff2") format("woff2"),
        url("../fonts/libre-franklin-v7-latin-700.woff") format("woff"),
        url("../fonts/libre-franklin-v7-latin-700.ttf") format("truetype"),
        url("../fonts/libre-franklin-v7-latin-700.svg#LibreFranklin")
            format("svg");
}

@font-face {
    font-family: "EB Garamond";
    font-style: normal;
    font-weight: 400;
    src: url("../fonts/eb-garamond-v15-latin-regular.eot");
    src: local(""),
        url("../fonts/eb-garamond-v15-latin-regular.eot?#iefix")
            format("embedded-opentype"),
        url("../fonts/eb-garamond-v15-latin-regular.woff2") format("woff2"),
        url("../fonts/eb-garamond-v15-latin-regular.woff") format("woff"),
        url("../fonts/eb-garamond-v15-latin-regular.ttf") format("truetype"),
        url("../fonts/eb-garamond-v15-latin-regular.svg#LibreFranklin")
            format("svg");
}

body {
    font-family: "EB Garamond";
    font-weight: 400;
    color: black;
    text-align: center;
    padding: 0;
    margin: 0;
    background-image: url("../images/background.png");
    background-repeat: no-repeat;
    background-attachment: fixed;
    background-size: cover;
    background-position: center;
}

h1,
h2,
h3,
h4,
h5,
h6 {
    font-family: "Libre Franklin";
    font-weight: 700;
}

.name-plate-h1 {
    font-size: 70px;
    line-height: 1.4;
}

.platform-link-icon-img {
    height: 50px;
    margin: 30px 0px;
}

.platform-links-container {
    max-width: 350px;
    margin: 25px auto;
}

.section-body-row {
    font-size: 20px;
    color: black;
}

@media (min-width: 1024px) {
    .name-plate-h1 {
        font-size: 188px;
        margin-top: 150px;
    }

    .platform-link-icon-img {
        margin: 50px 0px;
    }

    .platform-links-container {
        max-width: 900px;
    }

    .section-body-row {
        margin: 60px auto;
    }
}
